body {
  height: 100vh;
  font-family: "Libre Franklin", sans-serif;
}

h1, p {
  text-align: center;
}

h1 {
  font-size: 3rem;
}

p {
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.red {
  color: hsl(354, 100%, 66%);
  font-size: 1.3rem;
  font-style: italic;
  grid-row: 2/3;
}

@media (min-width: 768px) {
  .red {
    margin-top: 0;
    text-align: left;
  }
}
.inputError {
  border: 1px solid hsl(354, 100%, 66%) !important;
}

.header__logo {
  background-image: url(../img/logo.svg);
  background-position: center center;
  background-repeat: no-repeat;
  margin: 0 auto;
  height: 10rem;
  width: 10rem;
}

.header__title, .header__text {
  margin: 0 auto;
}

.header__title {
  font-weight: 300;
  padding: 2rem;
}
.header__title span {
  font-weight: 700;
}

@media (min-width: 768px) {
  .header__title {
    font-size: 4rem;
  }
  .header__text {
    margin-bottom: 2rem;
  }
}
.form {
  padding: 0.5rem;
  margin: 0 auto;
}
.form .form__input {
  display: block;
  width: 100%;
  padding: 1.2rem;
  border-radius: 1rem;
  border: 1px solid hsl(223, 87%, 63%);
  margin-bottom: 1rem;
}
.form .form__input::placeholder {
  padding-left: 2rem;
}
.form .form__button {
  width: 100%;
  padding: 1.4rem;
  border-style: none;
  background-color: hsl(223, 87%, 63%);
  border-radius: 1.5rem;
  color: #fff;
  -webkit-box-shadow: 0px 10px 0px 0px rgba(194, 211, 255, 0.11);
  -moz-box-shadow: 0px 10px 0px 0px rgba(194, 211, 255, 0.11);
  box-shadow: 0px 10px 0px 0px rgba(194, 211, 255, 0.11);
}
.form .form__button span {
  font-weight: 700;
}
.form .form__button:hover {
  cursor: pointer;
}

@media (min-width: 768px) {
  .form {
    display: grid;
    grid-template-columns: 2fr 1fr;
    /* grid-auto-flow: column; */
    gap: 2rem;
    width: 50%;
    max-width: 120rem;
    margin: 0 auto;
  }
  .form .form__input {
    grid-column: 1/2;
    padding: 0;
    margin: 0;
  }
  .form .form__button {
    grid-column: 2/3;
  }
}
.content {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.content .dashboard {
  margin: 8rem 0;
  /*   width: 90rem; */
  width: 35rem;
  /*  height: 70rem; */
  object-fit: contain;
}
@media (min-width: 768px) {
  .content .dashboard {
    margin: 0;
    width: 90rem;
    height: 70rem;
  }
}

.social {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}
.social .social__icon {
  width: 4rem;
  height: 4rem;
  border: 1px solid hsl(223, 100%, 88%);
  border-radius: 50%;
  padding: 1rem;
}
.social .social__icon:hover {
  background-color: #3f5b75;
  color: red;
}

footer {
  padding: 5rem 0;
}
footer .copyright {
  color: hsl(0, 0%, 59%);
  font-size: 1.8rem;
}